                                    </p>
                                </v-card-title>
                            </v-card>
                        </template>
                        <template v-slot:[`item.WebCheckIn`]="{ item }">
                            <v-btn class="mx-2 rounded-pill  pl-6 pr-6" color="#E8EFFE" depressed>
                                <v-icon dark>
                                    mdi-checkbox-marked-outline {{ item.WebCheckIn }}
                                </v-icon>
                                Check in
                            </v-btn>
                        </template>
                        <template v-slot:[`item.moreInfo`]="{ item }">
                            <v-btn class="mx-2 rounded-pill gradiant-blue pl-6 pr-6" dark>
                                More Info {{ item.moreInfo }}
                            </v-btn>
                        </template>
                    </v-data-table>
                </v-container>
            </v-tab-item>
            <v-tab href="#tab2" class="border-radiusafter"> International </v-tab>
            <v-tab-item value="tab2">
                <v-container>
                    <v-app-bar width="45%" class="mx-auto rounded-xl mb-10" color="#E8EFFE" flat>
                        <v-avatar class="ma-3" size="24" tile>
                            <v-img :src="require('../../views/flights/images/global.svg')"></v-img>
                        </v-avatar>
                        <v-spacer></v-spacer>
                        <v-toolbar-title class="text-center">See a list of all our flight destinations</v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-btn class="mx-2 rounded-pill gradiant-blue" dark>
                            Explore
                        </v-btn>
                    </v-app-bar>
                    <v-data-table :headers="headersdepartureslist" :items="departureslist" :search="search" class="mytable">
                        <template v-slot:[`item.CallSign`]="{ item }">
                          <div class="text-uppercase">{{
                            item.CallSign
                            }}</div></template>
                        <template v-slot:[`item.img`]="{ item }">
                            <v-card flat color="transparent" class="pt-6 pb-6">
                                <v-card-title class="pa-0 ml-6">
                                    <v-avatar tile size="64">
                                        <img :src="item.img" class="" />
                                    </v-avatar>
                                    <p class="mb-0 ml-6">
                                        {{ item.name }}
                                    </p>
                                </v-card-title>
                            </v-card>
                        </template>
                        <template v-slot:[`item.WebCheckIn`]="{ item }">
                            <v-btn class="mx-2 rounded-pill  pl-6 pr-6" color="#E8EFFE" depressed>
                                <v-icon dark>
                                    mdi-checkbox-marked-outline {{ item.WebCheckIn }}
                                </v-icon>
                                Check in
                            </v-btn>
                        </template>
                        <template v-slot:[`item.moreInfo`]="{ item }">
                            <v-btn class="mx-2 rounded-pill gradiant-blue pl-6 pr-6" dark>
                                More Info {{ item.moreInfo }}
                            </v-btn>
                        </template>
                    </v-data-table>
                </v-container>
            </v-tab-item>
        </v-tabs>
        <v-row align="center" justify="center" :class="{ charmSmall: $vuetify.breakpoint.smAndDown }">
            <v-col cols="12">
                <v-card flat class="mx-auto text-center">
                    <v-card-subtitle class="pl-5 text-uppercase page-title">
                        our routes
                    </v-card-subtitle>
                    <v-card-subtitle class="page-sub-title">
                        <h2>Flying to 50+ destinations</h2>
                    </v-card-subtitle>
                </v-card>
            </v-col>
        </v-row>
        <v-container>
            <div class="rounded-xl" id="map"></div>
        </v-container>
    </div>
</template>
<style scoped>
.gradi-icon {
    background: linear-gradient(90deg, #00c6ff 0%, #0072ff 100%);
    border-radius: 100px;
    font-style: inherit;
}
</style>
<script>
    import mapboxgl from "mapbox-gl";

//marker;
// map;
export default {
  data() {
    return {
      MyaccessToken:
        "pk.eyJ1IjoibWVycmlsbGdvbnNhbHZlcyIsImEiOiJjajN6ampkbzcwM3VxMzJyemR5Y2dqdTZuIn0.EGosEkXFJM5K_82Vy0XuCA",
      //map,
      search: "",
      filter: {},
      headers: [
        {
          class: 'header-airline',
          text: "Airline",
          
          filterable: false,
          value: "img",
          sortBy: "flight",
        },
        //{ text: "", value: "name", align:"left" },

        { text: "Call Sign", value: "CallSign" },
        { text: "Airline Contact No.", value: "AirlineContactNo" },
        { text: "Web Check-in", value: "WebCheckIn" },
        { text: "", value: "moreInfo", sortable: false },
      ],
      headersdepartureslist: [
        {
          class: 'header-airline',
          text: "Airline",
          
          filterable: false,
          value: "img",
          sortBy: "flight",
        },
        //{ text: "", value: "name", align:"left" },

        { text: "Call Sign", value: "CallSign" },
        { text: "Airline Contact No.", value: "AirlineContactNo" },
        { text: "Web Check-in", value: "WebCheckIn" },
        { text: "", value: "moreInfo", sortable: false },
      ],
      allflights: [{ text: "All" }, { text: "Indigo" }, { text: "Emirates" }],
      allterminals: [{ text: "All" }, { text: "1" }, { text: "2" }],
      flightlist: [
        {
          img: require("@/assets/images/flights/flightimg.svg"),
          flightlistId: "1",
          name: "Indigo",

          CallSign: "G9",
          AirlineContactNo: "+914422566072",
        },
        {
          img: require("@/assets/images/flights/emirates.svg"),
          flightlistId: "2",
          name: "Emirates",

          CallSign: "RED KNIGHT",
          AirlineContactNo: "+914422566072",
        },
        {
          img: require("@/assets/images/flights/thai.svg"),
          flightlistId: "3",
          name: "Thai Airways International PCL",

          CallSign: "thai",
          AirlineContactNo: "+18001021225",
        },
      ],

      departureslist: [
        {
          img: require("@/assets/images/flights/flightimg.svg"),
          flightlistId: "1",
          name: "Indigo",

          CallSign: "G9",
          AirlineContactNo: "+914422566072",
        },
        {
          img: require("@/assets/images/flights/emirates.svg"),
          flightlistId: "2",
          name: "Emirates",

          CallSign: "RED KNIGHT",
          AirlineContactNo: "+914422566072",
        },
        {
          img: require("@/assets/images/flights/thai.svg"),
          flightlistId: "3",
          name: "Thai Airways International PCL",

          CallSign: "thai",
          AirlineContactNo: "+18001021225",
        },
      ],
      placesData: [
        {
          description: "Chennai",
          coordinates: [80.21117527116763, 13.008852534856274],
        },
        {
          description: "Dubai",
          coordinates: [55.29004174850411, 25.280927196903818],
        },
        {
          description: "America",
          coordinates: [-77.090372, 38.881189],
        },
      ],
    };
  },

  methods: {
    setupMapBox() {
      mapboxgl.accessToken = this.MyaccessToken;
      this.map = new mapboxgl.Map({
        container: "map",

        //style: 'mapbox://styles/mapbox/streets-v9',
        style: "mapbox://styles/mapbox/light-v11",
        zoom: 2,
        projection: {
          name: "equirectangular",
          center: [80.16684476066258, 12.983596468763904],
        },
      });
    },
    markers(coordinates=[80.16684476066258, 12.983596468763904]) {
      this.marker1 = new mapboxgl.Marker({ "color": "#0072FF" })
        .setLngLat(coordinates)
        .addTo(this.map);
    },
    markerPopup() {
      this.popup = new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      });
    },

    rowClick(row) {
      this.$router.push("airlinedetails/");
      console.log(row.name);
      
    },
  },
  mounted() {
    this.setupMapBox();
    this.placesData.forEach(markerData=>{
        this.markers(markerData.coordinates)
    })
    this.markers();
    this.markerPopup();

    this.map.on("load", () => {
      this.map.addSource("places", {
        type: "geojson",
        data: {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                description: "<strong>Chennai</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [80.21117527116763, 13.008852534856274],
              },
            },
            {
              type: "Feature",
              properties: {
                description: "<strong>Dubai</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [55.29004174850411, 25.280927196903818],
              },
            },
            {
              type: "Feature",
              properties: {
                description: "<strong>America</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [-77.090372, 38.881189],
              },
            },
          ],
        },
      });

      this.map.addLayer({
        id: "places",
        //type: "symbol",
        source: "places",
        'type': 'circle',
          /*'paint': {
            'circle-radius': 10,
            'circle-color': '#448ee4'
          }*/
      });

      this.map.on("mouseenter", "places", (e) => {
        // Change the cursor style as a UI indicator.
        this.map.getCanvas().style.cursor = "pointer";

        // Copy coordinates array.
        const coordinates = e.features[0].geometry.coordinates.slice();
        const description = e.features[0].properties.description;
        console.log(this.description);

        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
          coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        }

        // Populate the popup and set its coordinates
        // based on the feature found.
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });

      this.map.on("mouseleave", "places", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
  },
};
</script>
